import axios from "axios";
import qs from "qs";
import { Toast } from "vant";

const isDevMode = true;
const baseURL = isDevMode
  ? 'https://immaster-xs.dsceshi.cn/'
  : 'https://biubiu.xinshangyun.com/';
const http = axios.create();
http.defaults.withCredentials = true;
http.defaults.headers.post["Content-Type"] =
  "application/x-www-form-urlencoded";
http.defaults.baseURL = baseURL
http.interceptors.request.use(
  config => {
    if (config.method === "post") {
      if (config.data && config.data.constructor === FormData) {
        return config;
      }
      if (!config.data) {
        config.data = {};
      }
      config.data.device = "mobile"
      config.data.appid = "appid1532576417089"
      config.data = qs.stringify(config.data);
    }
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

http.interceptors.response.use(
  response => {
    let data = response.data;
    if (typeof data === "object" && data.status !== 1) {
      Toast(data.info);
      return Promise.reject(response.data);
    }
    return response.data;
  },
  error => {
    return Promise.reject(error);
  }
);
export default http;
