<template>
  <router-view  v-show="web3"/>
</template>

<script>
import Web3 from "web3";
import { reactive, toRefs, onMounted} from "vue";
import {useStore} from 'vuex';
export default {
  setup() {
    const store = useStore()
    let data = reactive({
      web3: {},
      web3Provider: {},
      getBlance: (account) => {
        data.web3.eth.getBalance(account).then((amount) => {
          store.commit(
            "updateCurrentBalance",
            data.web3.utils.fromWei(amount, "ether")
          );
        });
      },
      initWeb3: async () => {
        if (window.ethereum) {
          data.web3Provider = window.ethereum;
          data.web3 = new Web3(data.web3Provider);
          try {
            await window.ethereum.enable();
          } catch (error) {}
        } else {
          if (window.web3) {
            data.web3Provider = window.web3.currentProvider;
            data.web3 = new Web3(data.web3Provider);
          } else {
            data.web3Provider = new Web3.providers.HttpProvider(
              "http://127.0.0.1:7545"
            );
            data.web3 = new Web3(data.web3Provider);
          }
        }
        store.commit("updateIsConnected", true);
        store.commit("updateProvider", data.web3);
        data.web3.eth.getAccounts().then((accounts) => {
          store.commit("updateAccount", accounts);
          return data.getBlance(accounts[0]);
        });
      }
    });
    onMounted( async () => {
      await data.initWeb3()
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
};
</script>

