import http from "../utils/http";
export function getCoinSrcList() {
  return http.post("/api/v1/user/coinconvert/CoinSrcList");
}
export function getCoinDstList(params) {
  return http.post("/api/v1/user/coinconvert/CoinDstList", params);
}
export function postInsert(params) {
  return http.post("/api/v1/user/coinconvert/Insert", params);
}